.app__laurels {
    position: relative;
}

#awards {
    align-items: flex-end;
}

.app__laurels-logo {
    position: absolute;
    padding: 20px;
    inset: 0;
    z-index: 10;
}

.app__laurels-logo img{
    width: 100px;
    height: 100px;
}

.app__laurels-awards {
    display: flex;
    align-items: flex-start;
    justify-content: flex-start;
    flex-wrap: wrap;
    margin-top: 3rem;
}

.app__laurels-awardCard {
    flex: 1;
    min-width: 240px;
    margin: 1rem;
    display: flex;
    align-items: flex-start;
    justify-content: flex-start;
}

.app__laurels-awardCard img {
    width: 50px;
    height: 50px;
}

.app__laurels-awardCard-content {
    display: flex;
    flex-direction: column;
    margin-left: 1rem;
}

@media screen and (max-width: 1150px) {
    .app__laurels-content {
        margin-top: 6.5rem;
    }
}

@media screen and (max-width: 670px) {
    .app__laurels-awardCard {
        min-width: 300px;
    }

    .app__laurels-awardCard {
        margin: 1rem 0;
    }
}

@media screen and (max-width: 450px) {
    .app__laurels-logo img{
        width: 75px;
        height: 75px;
    }

    .app__laurels-content {
        margin-top: 4rem;
    }

    .app__laurels-awardCard {
        min-width: 225px;
    }
}